import { InjectionToken } from '@angular/core';
import { GetPaymentOrdersRequestParams } from '@backbase/data-ang/payment-order';

export const ManageUpcomingAndHistoricalPaymentsJourneyConfigurationToken =
  new InjectionToken<ManageUpcomingAndHistoricalPaymentsJourneyConfiguration>(
    'ManageUpcomingAndHistoricalPaymentsJourneyConfigurationToken injection token',
  );

export interface ManageUpcomingAndHistoricalPaymentsJourneyConfiguration {
  notificationDismissTime: number;
  maskAccounts: boolean;
  upcomingPaymentParams: GetPaymentOrdersRequestParams;
  historyPaymentParams: GetPaymentOrdersRequestParams;
  paymentTypeInfoTexts: BaseObject<string>;
  journeyTitle: string;
  uiComponentProps?: UiComponentProps;
}

export interface UiComponentProps {
  mapCurrency: boolean;
}

export interface BaseObject<T> {
  [key: string]: T;
}
